import axios from 'axios';
import React, { useState } from 'react';
import { useNavigate } from 'react-router';
import { Link } from 'react-router-dom';

const InquiryForm = () => {
    const [title, setTitle] = useState("");
    const [inquiry, setInquiry] = useState("");

    const nav = useNavigate();

    // 
    const addQnaEvent = () => {
        axios.post("/user/inquiry/add", { title, inquiry }).then((res) => { 
            //    
            nav("/user/inquiry");
        });
    };
    return (
        <div className='mx_30 inquiry_form'>
            <div className='mt-1 fs_14 col_blue2'>
                <Link to="/user">  {'>'} </Link>
                <Link to="/user/inquiry">1:1  {'>'} </Link>
                <Link to="/user/inquiry/form">1:1 </Link>
            </div>
            <div className='fs_25 fw_700'>1:1 </div>

            <div className='mt_45 fw_500'> , <br/> ?</div>

            <div className='mt_45'>
                <div className='fs_18 fw_700'></div>
                <input type='text' className='form-control mt_10' onChange={(e)=>{
                    setTitle(e.target.value)
                }} placeholder=' .'/>
            </div>

            <div className='form-group mt_25'>
                <div className='fs_18 fw_700'> </div>
                <textarea type='text' className='form-control mt_10' style={{ height: "200px" }} onChange={(e)=>setInquiry(e.target.value)} placeholder='  .'/>
            </div>

            <div className='mt_10 fs_14 text-end fw_500'>
                 &nbsp;
                <Link to="/user" className='col_blue2'>'  {'>'} </Link>
                <Link to="/user/inquiry" className='col_blue2'>1:1 '</Link>&nbsp;  .
            </div>

            <div className='d-flex mt_45 inquiry_btn justify-content-evenly'>
                    <button type='button' onClick={() => nav('../')}>
                          
                    </button>  
                    <button type='button' onClick={addQnaEvent}>
                         
                    </button>
            </div>
        </div>
    );
};

export default InquiryForm;