import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import InquiryRowItem from './InquiryRowItem';

const Inquiry = () => {
    const [list, setList] = useState([]);

    const nav = useNavigate(); 

    const qnaList = () => {
        axios.post("/user/inquiry").then((res)=>{
            console.log(res.data.qna);
            setList(res.data.qna);
        })
    }

    useEffect(()=>{
        qnaList();
    }, []);
    return (
        <div className='mx_30'>
            <div className='mt-1 fs_14 col_blue2'>
                <Link to="/user">  {'>'} </Link>
                <Link to="/user/inquiry">1:1 </Link>
            </div>
            <div className='fs_25 fw_700'> </div>

            <div className='mt_45 fw_500'> , <br/> ?</div>

            <div className='d-flex mt_45 inquiry_list align-items-center'>
                <div className='me-auto fs_18 fw_700'>
                    1:1 
                </div>
                <div className='ml-auto'>
                    <button  onClick={() => nav('form')}>
                        <img alt='' src={require('../../../image/ico_inquiry.png')} className='img-fluid'/>
                        <span className='mx-2'>1:1 </span>
                    </button>
                </div>
            </div>
            
            <div className='mt_10'>
                <div className=''>
                {list &&
                    list.map((row, idx) => (
                        <InquiryRowItem
                        key={idx}
                        row={row}
                        />
                    ))}
                </div>
            </div>
        </div>
    );
};

export default Inquiry;