import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router';
import { Link } from 'react-router-dom';

const InquiryDetail = () => {
    const { inquirycode } = useParams();
    const nav = useNavigate();
    const [selectQnaData, setSelectQnaData] = useState("");

    //inquiry  dto  selectQnaData 
    const getSelectQnaData = () => {
        console.log(inquirycode);
        //const url = "/user/inquiry/select?inquirycode="+inquirycode;
        //axios.post(url)
        const url = "/user/inquiry/select";
        axios.post(url, {inquriycode:inquirycode})
          .then(res => {
            setSelectQnaData(res.data);
          })
          .catch(error => {
            console.error("Error fetching inquiry data:", error);
          });
    };

    //    
    useEffect(() => {
        console.log("detail,useEffect");
        getSelectQnaData();
    }, []);

    return (
        <div className='mx_30'>
            <div className='mt-1 fs_14 col_blue2'>
                <Link to="/user">  {'>'} </Link>
                <Link to="/user/inquiry">1:1  {'>'} </Link>
                <Link to="/user/inquiry/detail/:inquirycode">1:1  </Link>
            </div>
            <div className='fs_25 fw_700'>1:1  </div>

            <div className='mt_45'>
                <div className='fs_18 fw_700'>
                    Q. {selectQnaData.title}
                </div>
                <div className='mt_10 bg_red bor_red br_5 p-2' style={{height:"200px"}}>
                    {selectQnaData.inquiry}
                </div>
            </div>

            <div className='mt_25'>
                <div className='fs_18 fw_700'>
                    A.   
                </div>
                <div className='mt_10 bg_blue bor_blue1 br_5 p-2' style={{height:"200px"}}>
                    {selectQnaData.answer ? selectQnaData.answer : '   .'}
                </div>
            </div>

            <div className='d-flex mt_45 inquiry_btn justify-content-evenly'>
                    <button type='button' onClick={() => nav('../form')}>
                         
                    </button>
                    <button type='button' onClick={() => nav('../')}>
                          
                    </button> 
            </div>
        </div>
    );
};

export default InquiryDetail;