import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import AccountCircleOutlinedIcon from '@mui/icons-material/AccountCircleOutlined';

const MemberManage = () => {
    const nav = useNavigate();
    const [members, setMembers] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');

    useEffect(() => {
        const fetchMembers = async () => {
            try {
                const response = await axios.post("/admin/memberlist");
                setMembers(response.data.user);
            } catch (error) {
                console.error('   :', error.message);
            }
        };

        fetchMembers();
    }, []);

    const handleSearch = (e) => {
        setSearchTerm(e.target.value);
    };

    /* toLowerCase():        */
    const filteredMembers = members.filter(member =>
        member.nickname.toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <div className='mx_30'>
            <div className='mt-1 fs_14'>
                <Link to="/admin" className='col_blue2'>  {'>'} </Link>
                <Link to="/admin/MemberManage" className='col_blue2'></Link>
            </div>
            <div className='fs_25 fw_700'> </div>
            <br /><br />
            <h6 className='fs_16 fw_700'> </h6>

            {/*  */}
            <input
                type="text"
                placeholder="   &nbsp;"
                value={searchTerm}
                onChange={handleSearch}
                className="form-control mb-3 bg_red col_gray fs_16 fw_800"
                style={{ '::placeholder': { color: 'lightgray' } }}

            />
            <br />
            <h6 className='fs_16 fw_700'> </h6>
            <table className='table'>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th> </th>
                    </tr>
                </thead>
                <tbody className='bg_red'>
                    {filteredMembers.map(member => (
                        <tr
                            key={member.usercode}
                            onClick={() => nav(`MemberProfile`)}
                            style={{ cursor: 'pointer' }}
                        >
                            <td>{member.usercode}</td>
                            <td>{member.nickname}</td>
                            <td>{member.registereddate}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default MemberManage;
