import React from 'react';
import { useNavigate } from 'react-router-dom';

/*
[parameter information]

1. routes
 navbar        .
  name url  .
url    , url   .

  .
------------------------------------------------------
const CURRENT_ROUTES = [
    { name: 'TODAC ', url: '/user/chat' },
    { name: '', url: '/user/chat/counsel' }
];
------------------------------------------------------

2. title
   .
      .

  .
------------------------------------------------------
const PAGE_TITLE = 'TODAC ';
------------------------------------------------------
*/
const PageHeader = ({ routes, title }) => {
    const nav = useNavigate();

    return (
        <div className='pageheader'>
            <div className='fs_14 fw_500 col_blue2'>
                {
                    routes.map((data, index, origin) => (
                        <span key={index}>
                            <span className='header-clickable'
                                onClick={() => {
                                    if (data.url.length > 0)
                                        nav(data.url);
                                }}>
                                {data.name}
                            </span>
                            {(index < origin.length - 1) ? ' > ' : ''}
                        </span>
                    ))
                }
            </div>
            <div className='fs_24 fw_700'>
                {title}
            </div>
        </div>
    );
};

export default PageHeader;