import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Swal from 'sweetalert2';

const SignupNaver = () => {
  const [loading, setLoading] = useState(false);
  const userInfo = JSON.parse(window.sessionStorage.getItem('userInfo'));
  

  useEffect(()=>{
    Swal.fire({
      html: `<b style={{color : "#5279FD",fontSize : "5px"}}>
          . <br/>
         . <br/><br/>
            <br/>  .<br/>
        </b>`,
      confirmButtonColor: '#FF7170',
      background: '#F9EAEB'
    });
  },[]);

  const navigate = useNavigate();

  const goBack = () => {
      //   
      navigate(-2);
  };

  const CustomButton = styled(Button)({
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 16,
    padding: '6px 12px',
    border: '1px solid',
    lineHeight: 1.5,
    backgroundColor: '#FF7170', //   
    borderColor: '#FF7170', //   
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:hover': {
      backgroundColor: '#FF5E62', // hover    
      borderColor: '#FF5E62', // hover    
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#FF494D', //     
      borderColor: '#FF494D', //     
    },
    '&:focus': {
      boxShadow: '0 0 0 0.2rem rgba(255,113,112,.5)',
    },
  });

  const handleSignup = () => {
    setLoading(true);

    axios.post("/login/signupinsert", {
      userid: userInfo.id,
      nickname: userInfo.nickname,
      photo: userInfo.profile_image,
      type : "user"
    })
    .then( res => {
      //  
      if (res.data.result === 'success') {
        Swal.fire({
          icon: 'success',
          title: ' ',
          text: '  .    .',
          confirmButtonColor: '#FF7170',
          background: '#F9EAEB'
        });
        sessionStorage.removeItem("token");
        sessionStorage.removeItem("id");
        navigate('/login');  
      } 
      else {
        //  
        Swal.fire({
          icon: 'error',
          title: ' ',
          text: ' .  .',
          confirmButtonColor: '#FF7170',
          background: '#F9EAEB'
        });
      }
    });
  };

  return (
      <div style={{paddingTop : "55px", paddingRight : "55px", paddingLeft : "55px"}}>
          <ArrowBackIcon onClick={goBack} style={{ cursor: 'pointer' }} />
          <br/><br/>
          <h1 style={{color : "#FF494D", textAlign: "center",
                          fontSize : "3em", fontWeight : "1000"
                          }}>TODAC</h1>
          <br/>
          <h6 style={{textAlign: "center",color : "#536179"}}>[ ]</h6>
          <img alt="User Profile" src={userInfo.profile_image} 
          style={{ width: "150px", height : "150px", display: 'block', margin: '0 auto'}}/>
          <br/>
          <h6 style={{textAlign: "center",color : "#536179"}}>[]</h6>
          <input type='text' className='form-control' 
          style={{ width: "150px",textAlign: "center",margin: '0 auto'}}
          value={userInfo.nickname} placeholder="" readOnly />
          <br/><br/>
          
          <CustomButton variant="contained"
              style={{width : "100%"}}
              onClick={handleSignup}
              disabled={loading}
              >{loading ? ' ...' : ''}</CustomButton>
          <p onClick={goBack} style={{ cursor: 'pointer' }}>  </p>
      </div>
      

  );
};

export default SignupNaver;